body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
  height: 100%;
}

.ant-btn-primary {
  background-color: #a85100 !important;
  border-color: #a85100 !important;
}

.ant-spin {
  color: #a85100 !important;
}

.ant-spin-dot i{
  background-color: #a85100 !important;
}

.swiper-slide, .firstPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  height: 175px;
  bottom: 0;
}

.login {
  display: flex;
  align-items: center;
  justify-content: center;
}

.github {
  margin-right:5px;
}

.App, .firstPage {
  height: 100%;
}

.stress {
  color: orangered;
  font-weight: bold;
  margin-left: 5px;
  margin-right: 5px;
}

.failed {
  position: absolute;
  top:0;
}

.spin {
  position: absolute;
  bottom: 8%;
  width: 100%;
}

.qrcode {
  position: fixed;
  bottom: 10px;
  left: 10px;
  width: 40px;
  height: 40px;
  z-index: 100;
  box-shadow: none;
}
.swiper-slide, .firstPage, .page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login {
  display: flex;
  align-items: center;
  justify-content: center;
}

.github {
  margin-right:5px;
}

.App, .firstPage {
  height: 100%;
}

.stress {
  color: rgb(186,44,27);
  font-weight: bold;
  margin-left: 5px;
  margin-right: 5px;
}

a {
  color: rgb(186,44,27);
  font-weight: bold;
  font-size: 16px;
  margin-left: 5px;
  margin-right: 5px;
  text-decoration: underline;
}

a:visited {
  color: rgb(186,44,27);
  font-weight: bold;
  margin-left: 5px;
  margin-right: 5px;
  text-decoration: underline;
}

strong {
  color: rgb(186,44,27);
  font-weight: bold;
}

.mb5 {
  margin-bottom: 5px;
}
.mb20 {
  margin-bottom: 20px;
}
.saveSuccess {
  position: absolute;
  top:0;
  left:0;
  right:0;
}

.avatar {
  width:100px;
}

p {
  margin: 2px;
  color: rgb(168, 81, 0);
  font-size: 16px;
}

h4, li {
  color: rgb(168, 81, 0);
  font-size: 16px;
}

span {
  font-size:16px;
}

button span {
  font-size:14px;
}

img {
  box-shadow: 0 0 0 rgb(168, 81, 0),
   0 0 0 rgb(168, 81, 0),
   1px 1px 5px rgb(168, 81, 0),
   1px 1px 5px rgb(168, 81, 0);
  width: 200px;
}

.qrcode {
  position: fixed;
  bottom: 10px;
  left: 10px;
  width: 40px;
  height: 40px;
  z-index: 100;
  box-shadow: none;
}
.page1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 40%;
}

.username {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: absolute;
  height: 175px;
  bottom: 0;
}

.typer {
  position: absolute;
  height: 150px;
  bottom: 0;
}

.arr{width: 10%;
  overflow: hidden;
  text-align: center;
  position: absolute;
  left: calc(50% - 10px);
  bottom:15px;
  -webkit-animation: twinkling 1s infinite ease-in-out;
}

.arr i{
  display: inline-block;
  width:20px;
  height:20px;
  border-width:0;
  border-right-width:2px;
  border-top-width:2px;
  border-style:solid;
  border-color:rgb(156,74,27);
  -webkit-transform: rotateZ(135deg);
          transform: rotateZ(135deg);
}
@-webkit-keyframes twinkling{    /*透明度由0到1*/
  0%{
      opacity:0; /*透明度为0*/
  }
  100%{
      opacity:1; /*透明度为1*/
  }
}
